const app = getApp();
import helper from '../componets/helper.js';
export default {
	data() {
		return {
			Inv: '0',
			banner:[{
				img: '../static/logo.png',
			},
			{img: '../static/logo.png'}],
			dataInfo:{},
			urlType: '',
			url:'',
			imgUrl:'',
			addressList: {}
		}
	},
	onLoad(option) {
		this.imgUrl = helper.imgUrl;
		console.log(option)
		let headTitle = option.type
		let navH = ''
		if(headTitle == '推荐图书' || headTitle == 'books'){
			navH = '图书详情'
			this.url = 'details/book'
			this.dataT = {
				book_id: option.id
			}
			this.goPay = 'books'
		}else{
			navH = '资料详情'
			this.url = 'details/data'
			this.dataT = {
				data_id: option.id
			}
			this.goPay = 'datas'
		}
		uni.setNavigationBarTitle({
			title: navH
		})
		this.getDetail()
		this.urlType = this.goPay
		this.getAdderssL()
	},
	onShow() {
		var arr = uni.getStorageSync('address') || [];
		console.info('缓存数据：' + arr);
		// // 更新数据
		this.addressList = arr;
	},
	methods: {
		// 选项卡
		navTap(e){
			this.Inv = e
		},
		// 获取数据
		getDetail(){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${helper.requestUrl}${this.url}`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.dataT,
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.dataInfo = res.data
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		goBuy(e){
			uni.navigateTo({
				url:`/pages/dataBuy?type=${this.urlType}&id=${e}`
			})
		},
		getAdderssL(){
			uni.request({
				url: `${this.$helper.requestUrl}user/getAddressList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						let temp = res.data
						 for(let i=0;i<res.data.length;i++){
								 if(temp[i].is_default == 1){
									 let add = temp[i]
									 this.addressList = add
									 uni.setStorageSync('address', add);
								 }
						 }
					} else {
						this.addressList = ''
					}
				}
			});
		},
		addAddress(e){
			let url = ''
			if(e == '0'){
				url = 'addersList'
			}else if(e== '1'){
				url = 'addersList'
			}
			uni.navigateTo({
				url: `/pages/${url}`
			})
		},
		
		
	}
}
